import request from '@/utils/request'

export function search (data) {
  return request({
    url: '/contact/search',
    method: 'post',
    data
  })
}

export function getPage (page, noitems) {
  return request({
    url: '/contact/search?page=' + page + '&item=' + noitems,
    method: 'post'
  })
}
